    background: rgb(30,87,153); /* Old browsers */ /* FF3.6-15 */ /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to bottom, rgb(30,87,153) 51%,rgb(32,124,202) 74%,rgb(41,137,216) 89%,rgb(41,137,216) 97%,rgb(41,137,216) 97%,rgb(32,124,202) 100%,rgb(32,124,202) 100%,rgb(32,124,202) 100%,rgb(125,185,232) 100%,rgb(32,124,202) 100%,rgb(32,124,202) 102%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#1e5799', endColorstr='#207cca',GradientType=0 ); /* IE6-9 */
